.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: none;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  flex-direction: column;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.divAvatarComentario{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin: 50px;
} 

#avatar{
  width: 60px;
}
#cervezaComent{
  width: 85px;
  padding: 10px;
}
@media(min-width: 768px){

  .mySwiperCerveza{
    width: 200px;
  }
  .swiper-slide{
    width: 200px;
  }
  .divSwiper{
    width: 100%;
    padding: 0  80px;
  }
  .divSwiper{
    width: 100%;
    padding: 0 30px;
  }
}